import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  Input,
  message,
  Checkbox
} from 'antd'
import style from './style/Form.scss'
import {API_WDENTRYORDERS_SUPPLIERS} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSearch} from 'Form'
import moment from 'moment'

const {RangePicker} = DatePicker
@connect(
  state => ({...state.withoutOrderInWarehouse, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一周 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preWeek = moment().subtract(7, 'd')
    update({searchParams: {initDate: [preWeek, moment()]}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    const {initDate = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('请选择创建时间后搜索')
    }

    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
    // 重置时间
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        initDate,
        supplierName,
        sourceOrderFlag,
        orderCode
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={12}>
            <div className='commonInput'>
              <label>订单查询：</label>
              <div className='box'>
                <Input style={{width: '400px'}} placeholder='支持各种单号查询' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_WDENTRYORDERS_SUPPLIERS}
              label="供应商"
              searchParams={{warehouseCode: userLoation.value}}
              searchParamsKey='supplierCode'
              selectItem={{data: supplierName, key: 'key', valueKey: ['key', 'value']}}
              onChangeSelect={(value) => {
                this.onInputChange({supplierName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({supplierName: value})
              }}
            />
          </Col>
          <Col span={12}>
            <div className='commonInput'>
              <label>创建时间：</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <Checkbox style={{marginLeft: 45}} checked={sourceOrderFlag ? true : false} onChange={(e) => {
              this.onInputChange({sourceOrderFlag: e.target.checked})
            }}>来源单未生成</Checkbox>
          </Col>
          <Col span={6}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



