import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'

@connect(
  state => ({...state.withoutOrderInWarehouse}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  create = () => {
    const {update} = this.props
    // let createTable = new Array(100).fill({})
    // createTable.map((o, i) => {
    //   createTable[i] = {id: i}
    // })
    update({isShowCreateModal: true, createTable: [], supplierCode: '', shipperCode: '', lastSupplierCode: ''})
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size} = this.props
    handleExport({searchParams: {page, size, ...searchParams}}, () => {
      message.success('导出成功')
    })
  }

  /* 打印预览 */
  handleBrowse = (record) => {
    const {selectedRowKeys} = this.props
    if (!selectedRowKeys.length) {
      message.error('请勾选订单')
      return
    }
    setVariant('wms/inWarehouseList', selectedRowKeys)
    window.open('/wms/inWarehouseList/Print/2', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  render() {
    return (
      <div className={style['table-tool']}>
        <Button type="primary" className={style.btn} onClick={this.create}>创建</Button>
        <Button type="primary" className={style.btn} onClick={this.handleBrowse}>打印收货单</Button>
        <Button type="primary" className={style.btn} onClick={this.handleExport}>导出</Button>
      </div>
    )
  }
}



