import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {getData} from 'util'
import {API_TEST} from 'api'

@connect(
  state => ({...state.withoutOrderInWarehouse, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '操作',
      width: 150,
      dataIndex: 'defaultStorageLocationFlag',
      key: 'defaultStorageLocationFlag',
      render: (text, record, index) => <div className="tableOperation">
        <span onClick={() => {
          this.onEdit('isShowRecordModal', record)
        }}>记录</span>
      </div>
    }, {
      title: '入库单',
      width: 150,
      dataIndex: 'orderCode',
      key: 'orderCode',
      render: (text, record, index) => <a onClick = { () => {
        const {history} = this.props
        window.open(`/wms/in-warehouse/inWarehouseList/${record.orderCode}`)
        // history.push(`/wms/in-warehouse/inWarehouseList/${record.orderCode}`)
      }}>{text}</a>
    }, {
      title: '供应商',
      width: 200,
      dataIndex: 'supplierName',
      key: 'supplierName'
    }, {
      title: '来源单号',
      width: 180,
      dataIndex: 'originOrderCode',
      key: 'originOrderCode'
    }, {
      title: '收货状态',
      width: 100,
      dataIndex: 'receiveStatusDesc',
      key: 'receiveStatusDesc'
    }, {
      title: '创建人',
      width: 100,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '创建时间',
      width: 150,
      dataIndex: 'createTime',
      key: 'createTime'
    }]
  }
  componentDidMount() {
    getData({api: API_TEST}).then(data => {
      // 相应的逻辑判断
      
    }).catch(err => {
      
    })

    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value ) return
    handleGetList({searchParams: {page, size, ...searchParams}})

    
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if ( newProps.userLoation.value != userLoation.value ) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }

  //一些操作
  onEdit = (modalType, record) => {
    const {update} = this.props
    update({[modalType]: true, modalId: [record.orderCode]})
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange =  (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: true, columns: sorter.field}})
      } else {
        handleGetList({searchParams: {page, size, ...searchParams, isAsc: false, columns: sorter.field}})
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, ...searchParams}})
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      }
      // getCheckboxProps: record => ({
      //   disabled: record.status !== '待审批' // Column configuration not to be checked
      // })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



